        }
      );
      dialog_from_visible.value = false;
      task_data_form_ref.resetFields();
      get_tasks("name");
    } else {
      return false;
    }
  });
};

// 重新运行该任务
const restart_click = async (domain_id: String) => {
  alert("this_is_restart_click");
  // await restart_task(domain_id);
  get_tasks("name");
};

// 删除该任务
const delete_task_click = async (domain_id: String) => {
  const domain_id_list = [domain_id];
  await delete_task(domain_id_list).then(
    () => {
      ElMessage({ message: "删除任务成功", type: "success" });
    },
    (error) => {
      ElMessage({ message: "删除任务失败", type: "error" });
    }
  );
  get_tasks("name");
};

// 批量删除
const multi_task_delete_button_ref = ref();
const multi_task_delete_click = () => {
  ElMessageBox.confirm(
    "proxy will permanently delete the file. Continue?",
    "Warning",
    {
      confirmButtonText: "OK",
      cancelButtonText: "Cancel",
      type: "warning",
    }
  )
    .then(async () => {
      await delete_task(multi_task_domain_id_list_ref.value).then(
        () => {
          ElMessage({ message: "删除任务成功", type: "success" });
        },
        (error) => {
          ElMessage({ message: "删除任务失败", type: "error" });
        }
      );
      get_tasks("name");
    })
    .catch(() => {
      ElMessage({
        type: "info",
        message: "Delete canceled",
      });
    });
};

// 批量导出任务的数据
const multi_export_click = async (export_type: String) => {
  if (export_type == "website") {
    await export_website(multi_task_domain_id_list_ref.value);
  } else if (export_type == "subdomain") {
    await export_subdomain(multi_task_domain_id_list_ref.value);
  } else if (export_type == "ip") {
    await export_private_ip(multi_task_domain_id_list_ref.value);
  } else if (export_type == "ip_port") {
    await export_ip_port(multi_task_domain_id_list_ref.value);
  }
  get_tasks("name");
};

// 批量选中的设置
const multi_task_domain_id_list_ref = ref([]);
const multi_delete_button_disabled = ref(true);
const multi_export_button_disabled = ref(true);
const handle_selection_table = (selection) => {
  if (selection.length) {
    multi_delete_button_disabled.value = false;
    multi_export_button_disabled.value = false;
  } else {
    multi_delete_button_disabled.value = true;
    multi_export_button_disabled.value = true;
  }

  multi_task_domain_id_list_ref.value = [];
  for (let i = 0; i < selection.length; i++) {
    multi_task_domain_id_list_ref.value.push(selection[i].domain_id);
  }
};

// 每页的条目数量变化的时候，这个一般就是在发起请求之后，收到了不一样的数据才会导致page_size的变化，比如第一次初始化的时候
const handle_size_change = (page_size) => {
  task_query_form.value.page_num = 1;
  task_query_form.value.page_size = page_size;
  // @ts-ignore
  get_tasks();
};

// 当前的第几页
const handle_current_change = (page_num) => {
  task_query_form.value.page_num = page_num;
  // @ts-ignore
  get_tasks();
};

// jump
const get_detail = (domain_id) => {
  sessionStorage.removeItem("current_tab");
  router.push({
    path: "/detail",
    query: {
      domain_id: domain_id,
    },
  });
};

/// 下拉菜单：导出模块
const export_down_menu = ref();
const show_click = () => {
  export_down_menu.value.handleOpen();
};

const select_value = ref("探测和利用");
const option_click = (option_value: string) => {
  if (option_value == "attack") {
