import service from "@/utils/http"

export const get_task = (data: any) => {
    const refactor_data = {page_num: 1, page_size: 1, query: '', search_type: ''}
    refactor_data.page_num = data.page_num
    refactor_data.page_size = data.page_size
    refactor_data.search_type = data.search_type
    if(data.query == ''){
        refactor_data.query = 'null'
    }else{
        refactor_data.query = data.query
    }
    return service.get(`/task/${refactor_data.search_type}/${refactor_data.page_num}/${refactor_data.page_size}/${refactor_data.query}`)
}

export const post_task = (data: any) => {
    return service.post('/task', data)
}

export const get_task_count = () => {
    return service.get('/task/count')
}

export const delete_task = (domain_id_list: any) => {
    return service.delete('/task', {data:domain_id_list})
}

export const export_website = (domain_id_list: any) => {
    return service.post('/common/website/export_website', {domain_id_list:domain_id_list})
}

export const export_private_ip = (domain_id_list: any) => {
    return service.post('/common/ip/export_private_ip',{domain_id_list:domain_id_list})
}

export const export_ip_port = (domain_id_list: any) => {
    return service.post('/common/ip/export_ip_port', {domain_id_list:domain_id_list})
}

export const export_subdomain = (domain_id_list: any) => {
    return service.post('/common/subdomain/export_subdomain', {domain_id_list:domain_id_list})
}

export const get_finished_task = (search_type: string) => {
    return service.get(`/task/count/${search_type}`)
}